.list{
    width:100%;
    margin-top:10px;
}
.itemcontainer{
    margin-left: 50px;
    display: flex;
    margin-top: 10px;
    width: max-content;
    transform: translateX(0px);
    transition: all .5s ease;
}

.watch {
    color: aliceblue;
    font-size: 20px;
    font-weight: 500;
    margin-left: 50px;
  }
  .wrapper {
    position: relative;
    
  }
  
  
  
  .wrapper .sliderArrowleft {
    left: 0;
    width: 50px;
    height: 100%;
    background-color: rgba(22, 22, 22, 0.5);
    color: white;
    position: absolute;
    z-index: 99;
    top: 0;
    bottom: 0;
    margin: auto;
    cursor: pointer;
  }
  
  .wrapper .sliderArrowright {
    right: 0;
    width: 50px;
    height: 100%;
    background-color: rgba(22, 22, 22, 0.5);
    color: white;
    position: absolute;
    z-index: 99;
    top: 0;
    bottom: 0;
    margin: auto;
    cursor: pointer;
  }
  